import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import {ScreenSpinner, Spinner} from '@vkontakte/vkui';
import { View, AdaptivityProvider, AppRoot, Button, Footer, Link, Panel, PanelHeader } from '@vkontakte/vkui';
import JsFileDownloader from 'js-file-downloader';
import '@vkontakte/vkui/dist/vkui.css';
import './App.css'
import SnackbarItem from './Snackbar';
import { Icon28DownloadCloudOutline } from '@vkontakte/icons';
import { Icon16WarningTriangle } from '@vkontakte/icons';

const App = () => {
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	let [link, setLink] = useState('');
	let [button, setButton] = useState(<Spinner/>)

	const getFile = () => {
		new JsFileDownloader({ 
			url: link
		  })
		  .then(function (data) {
			setPopout(<SnackbarItem code='success' text='Сообщение загружено!' setSnackbar={setPopout}/>)
		  })
		  .catch(function (error) {
			console.log(error);
			setPopout(<SnackbarItem code='error' text='Произошла ошибка!' setSnackbar={setPopout}/>)
		  });
	}

	const copy = () => {
		bridge.send('VKWebAppCopyText', { text: link }).then((data) => { 
			  if (data.result) {
				setPopout(<SnackbarItem code='success' text='Ссылка скопирована!' setSnackbar={setPopout}/>)
			  } else {
				setPopout(<SnackbarItem code='success' text='Произошла ошибка!' setSnackbar={setPopout}/>)
			  }
			}).catch((error) => {
			  console.log(error);
			});
	}

	useEffect(() => {
		setPopout(null)
		let hash = window.location.hash.slice(1, window.location.hash.length)
		let links = hash.split('&')
		
		if (links[1].length < 10) {
			setButton(<div className='downloand-error'>
				Перешлите <Link href='https://vk.com/voice_downloader' target='_blank'>боту</Link> голосовое сообщение и перейдите по полученой ссылке
			</div>)
		} else {
			setButton(<div className='downloand'>
			<Icon28DownloadCloudOutline style={{cursor: 'pointer'}} onClick={getFile} width={100} height={100}/>
				Скачать
			</div>)
		}
		setLink(links[1])
	}, []);

	useEffect(() => {
		console.log('ok');
	}, [button])


	return (
		<AdaptivityProvider>
			<AppRoot>
				<View activePanel='home' popout={popout}>
					<Panel id='home'>
						<PanelHeader>Voice Downloader</PanelHeader>

						<div className='main'>
							<div className='success'>
								<Icon28DownloadCloudOutline width={34} height='34' style={{margin: '0 auto'}}/>
								Вы скачиваете голосовое сообщение, нажмите на кнопку "скачать" и загрузка начнется автоматически!
							</div>
							<div className='warn'>
							<Icon16WarningTriangle width={34} height='34' style={{margin: '0 auto'}}/>
							Обратите внимание что не на всех устройствах доступна загрузка! Не работает на IOS и еще некоторых ОС
						</div>
						</div>

						{button}

						{link.length > 10 ? <div>
							<div className='link'>
							Прямая ссылка на сообщение: <Link href={link} target="_blank">{link}</Link>
						</div>

						<div className='link'>
							<Button size='s' onClick={copy}>Скопировать ссылку</Button>
						</div> 
						</div> : <div style={{marginTop: 20}} className='downloand-error'>Сылка на скачивание не найдена!</div>}

						<Footer>Made by <Link href='https://хор-енотов.рф'>XOP EHOTOB</Link></Footer>
					</Panel>
				</View>
			</AppRoot>
		</AdaptivityProvider>
	);
}

export default App;

